//can be changed
$stopwatch-diameter: 400px;
$stopwatch-color: black;

//fixed values
$stopwatch-dot-diameter: $stopwatch-diameter / 45;

$second-hand-height: $stopwatch-diameter / 1.7;
$second-hand-width: $stopwatch-dot-diameter / 4;

$minute-hand-height: $stopwatch-diameter / 2;
$minute-hand-width: $stopwatch-dot-diameter / 2.5;
